* { margin:0; padding:0; box-sizing:border-box;}

.App {
  height: 100vh;
  width: 100vw;
  background-color: white;
  overflow: hidden;
  text-align: center;
  transition: -webkit-filter 1s, filter 1s;
}

.App--inverted {
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}


input,
textarea,
button,
select,
a {
  z-index: 1;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

p {
  z-index: 1;
  font-size: calc(10px + 1vw);
  font-family: 'Inter', sans-serif;
}

.typewrite {
  font-size: 2em;
  z-index: 0;
  margin: 0;
  font-weight: 500;
  font-family: 'Inter', sans-serif;
  user-select: none;
}

.typewrite > .wrap { border-right: 0.08em solid black}

/* p,h1,h3 {
  font-family: 'Inter', sans-serif;
} */

a { 
  z-index: 1;
  color: white;
  text-decoration: none; }

a:hover {
  color: cyan;
}

/* selected link */
a:active {
  color: white;
  font-weight: 300;
  text-decoration: underline;
}

.App-header {
  z-index: 1;
  padding: 20px;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 0.5vw);
  color: white;
  width: 100%;
}

.App-category-title {
  user-select: none;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 20px;
  color: white;
  font-weight: 300;
}

.App-invert {
  width: 30px;
  height: calc(20px + 2vw);
  display: flex;
  cursor: pointer;
  flex-direction: column;
  margin-left: auto;
  align-content: center;
  justify-content: center;
  object-fit: contain;
}

.App-category-title-clicked {
  margin-top: 0;
  margin-right: 20px;
  color: white;
  font-weight: 300;
  text-decoration: underline;
  user-select: none;
}

.App-body {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  flex-direction: row;
  color: black;
}

@keyframes blurOut {
  0% {
    filter: blur(2px);
  }
  100% {
    filter: blur(0px);
  }
}

.App-footer {
  z-index: 1;
  bottom: 0;
  left: 0;
  padding: 20px;
  position: absolute;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 0.5vw);
  color: black;
  width: 100%;
}