.Page-body {
    padding: 10px;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.Page-content-container {
    align-items: center;
    width: 80%;
    height: min-content;
    overflow-y: scroll;
    padding: 20px;
}

.Page-title-container {
    position: relative;  
    text-align: left;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 5;
    }
    100% {
        opacity: 0;
    }
}

.Page-title-image {
    user-select: none;
    z-index:0;
    animation: fadeIn 3s normal forwards ease-in-out;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    filter: blur(1vh);
    width: 100vw;
}

.Page-title {
    font-family: 'Work Sans', sans-serif;
    font-weight: bold;
    user-select: none;
}

.Page-title2 {
    margin-top: 5px;
    font-size: 10vh;
    font-weight: normal;
    font-family: 'Libre Barcode 39', cursive;
    user-select: none;
    white-space: nowrap;
    overflow: hidden;
}

.Page-link { 
    z-index: 1;
    color: black;
    text-decoration: none; 
}
  
.Page-link:hover {
    color: cyan;
    font-weight: 400;
}

